// =============================================================================
// > LAYOUTS: SITE HEADER
// =============================================================================

.site-header {

    padding: 30px 0;
    line-height: 50px;

    // ==================================================
    // > BRAND
    // ==================================================
    &__brand {
        display: block;
        height: 50px;
        width: 50px;
        text-indent: -9999em;
        background: img("admin_logo.png") center center / contain no-repeat; }


    // ==================================================
    // > MAIN MENU
    // ==================================================
    &__nav {
        .menu {
            a {
                @include font(text);
                padding: 0 10px; }
            li {
                float: left; } } } }



